.wp-block-koicha-api-fnugg-card__title {
  margin: 0 !important;
  padding: 1rem;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background: #323232;
  color: #fffefe !important;
}

.wp-block-koicha-api-fnugg-card .wp-block-koicha-api-fnugg-card > img {
  width: 100%;
}

.wp-block-koicha-api-fnugg-card__overlay__sub__title {
  padding: 0.5rem;
  margin: 0 !important;
  width: 100%;
  background: #323232;
  text-align: center;
}

.wp-block-koicha-api-fnugg-card__overlay__sub__title > h4 {
  margin: 0 !important;
  color: #fffefe !important;
}

.wp-block-koicha-api-fnugg-card__overlay__sub__title > p {
  color: #fffefe !important;
}

.wp-block-koicha-api-fnugg-card--grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 0.5fr);
  gap: 1px 1px;
  grid-template-areas: "cloud degree" "wind description";
  text-align: center;
  border-bottom: 3px solid #40a3be;
}

.cloud {
  grid-area: cloud;
  padding: 2rem 0 !important;
}

.cloud > img {
  height: 100px !important;
}

.degree {
  grid-area: degree;
}

.degree > h1 {
  font-size: 8rem !important;
  color: #ff0000 !important;
}

.wind {
  grid-area: wind;
  margin: 5rem 0 !important;
}

.wind__row__1 {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.wind__row__2 {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.wind__row__1 > img {
  margin-top: 10px;
  margin-left: 4rem;
  width: 50px;
  height: 50px;
}

.wind__row__1 > h3 {
  margin-left: 1rem !important;
  font-weight: normal !important;
}

.wind__row__1 > h5 {
  margin: 14px !important;
  font-weight: normal !important;
}

.description {
  grid-area: description;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 4rem 0;
  padding: 1rem 0;
}

.description > img {
  width: 4rem !important;
  height: 4rem !important;
}

.description > p {
  margin: 1rem !important;
  font-size: 1.5rem !important;
}

#wp-block-koicha-api-fnugg__resort-name {
  margin: 2rem !important;
}